     margin: 0;     
    height: 100vh;
    width: 100vw;
    display: grid;
   grid-template-columns: 300px 1fr;
}

aside{
    height: 100%;
    background-color: #91C8E4;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    gap: 20px;
  
    
}

header{
    display: flex;
    justify-content: space-between;
    margin: 10px;
    align-items: center;
}

.topSection{
 display: flex;
 flex-direction: column;
 align-items: center;
 gap: 30px;
    align-items: start;
    margin-left: 20px;
}
.proyectsSection{
    display: flex;
 flex-direction: column;
 align-items: center;

}
.headerPro{
    width: 95%;
display: flex;
align-items: flex-start;
margin: 10px;
margin-bottom: 0;
justify-content: space-between;

}
.main{

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
}
.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
}
.rabbit{
    height: 200px;
    width: 200px;
}
.formProyect{
display: flex;
flex-direction: column;
gap: 5px;
}
.formTask{
    height:15vh ;
    width: 50vw;
display: flex;
flex-direction: column;
gap: 5px;
}
.enterTask{
    margin: 2px;
}
.buttons{
   align-self: flex-end;
   
}
.titleTask{
    font-size: 1.3rem;
    opacity: 0.7;
}
.formTask input{
    border: none;
}
.formTask .inputDate{
    border: 1px solid black;
    border-radius: 5px;
}
 input:hover{
    border: 1px solid back;
}
 .buttons{
    display: flex;
    gap: 10px;
}
select{
    align-items: flex-start;
}
.userInfo{
    display: flex;
    align-items: center;
    gap: 5px;
}

.topSection div {
 display: flex;
    align-items: center;
    gap: 5px;
    width: 90%;
    border-radius: 10px;
    padding: 3px;
}
.topSection div:hover{
    background-color: #f2f2f2;
    
}

.headerPro h2{
    margin-top: 0;
    margin-left: 10px;
    margin-bottom: 0;
}
.proyects{
    width: 100%;
}
li{
    width: 95%;
    list-style-type: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
   border-radius: 10px;
       padding: 3px;

}
 
li:hover{
    background-color: #f2f2f2;
    
}
.hash{
    font-weight: 100;
    font-size: 1.3rem;
}
ul{
    margin: 0;
    padding: 15px;
    padding-right: 0px;
}
.addProyectTask{
    width: auto;
}
.tasks{
    justify-content: flex-start;

    width:300px;
    border-bottom: 1px solid gray;
    border-radius: 0%;
    gap: 10px;
}
.delBtn{
    margin-left: 180px;
}
.divTasks{
    width: 100px;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;KACK,SAAS;IACV,aAAa;IACb,YAAY;IACZ,aAAa;GACd,gCAAgC;AACnC;;AAEA;IACI,YAAY;IACZ,yBAAyB;IACzB,aAAa;IACb,sBAAsB;IACtB,2BAA2B;IAC3B,SAAS;;;AAGb;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,YAAY;IACZ,mBAAmB;AACvB;;AAEA;CACC,aAAa;CACb,sBAAsB;CACtB,mBAAmB;CACnB,SAAS;IACN,kBAAkB;IAClB,iBAAiB;AACrB;AACA;IACI,aAAa;CAChB,sBAAsB;CACtB,mBAAmB;;AAEpB;AACA;IACI,UAAU;AACd,aAAa;AACb,uBAAuB;AACvB,YAAY;AACZ,gBAAgB;AAChB,8BAA8B;;AAE9B;AACA;;IAEI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;AACb;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;AACb;AACA;IACI,aAAa;IACb,YAAY;AAChB;AACA;AACA,aAAa;AACb,sBAAsB;AACtB,QAAQ;AACR;AACA;IACI,YAAY;IACZ,WAAW;AACf,aAAa;AACb,sBAAsB;AACtB,QAAQ;AACR;AACA;IACI,WAAW;AACf;AACA;GACG,oBAAoB;;AAEvB;AACA;IACI,iBAAiB;IACjB,YAAY;AAChB;AACA;IACI,YAAY;AAChB;AACA;IACI,uBAAuB;IACvB,kBAAkB;AACtB;CACC;IACG,sBAAsB;AAC1B;CACC;IACG,aAAa;IACb,SAAS;AACb;AACA;IACI,uBAAuB;AAC3B;AACA;IACI,aAAa;IACb,mBAAmB;IACnB,QAAQ;AACZ;;AAEA;CACC,aAAa;IACV,mBAAmB;IACnB,QAAQ;IACR,UAAU;IACV,mBAAmB;IACnB,YAAY;AAChB;AACA;IACI,yBAAyB;;AAE7B;;AAEA;IACI,aAAa;IACb,iBAAiB;IACjB,gBAAgB;AACpB;AACA;IACI,WAAW;AACf;AACA;IACI,UAAU;IACV,qBAAqB;IACrB,aAAa;IACb,mBAAmB;IACnB,8BAA8B;GAC/B,mBAAmB;OACf,YAAY;;AAEnB;;AAEA;IACI,yBAAyB;;AAE7B;AACA;IACI,gBAAgB;IAChB,iBAAiB;AACrB;AACA;IACI,SAAS;IACT,aAAa;IACb,kBAAkB;AACtB;AACA;IACI,WAAW;AACf;AACA;IACI,2BAA2B;;IAE3B,WAAW;IACX,6BAA6B;IAC7B,iBAAiB;IACjB,SAAS;AACb;AACA;IACI,kBAAkB;AACtB;AACA;IACI,YAAY;AAChB","sourcesContent":["body{\n     margin: 0;     \n    height: 100vh;\n    width: 100vw;\n    display: grid;\n   grid-template-columns: 300px 1fr;\n}\n\naside{\n    height: 100%;\n    background-color: #91C8E4;\n    display: flex;\n    flex-direction: column;\n    justify-content: flex-start;\n    gap: 20px;\n  \n    \n}\n\nheader{\n    display: flex;\n    justify-content: space-between;\n    margin: 10px;\n    align-items: center;\n}\n\n.topSection{\n display: flex;\n flex-direction: column;\n a